@import "sass/index";

.select {
  font-family: "Gilroy";
  background: use(--info-dark) !important;
  padding: 8px 10px;

  white-space: nowrap;
  color: use(--template-modal-title-color);
}
.selectLabel {
}
.selectExpandIcon {
  height: 100%;

  & svg {
    width: 12px;
    height: auto;
  }
}
.selectItem {
  text-transform: uppercase;
}

.selectBox {
  border-radius: 8px;
  display: flex;

  overflow: hidden;
}
.title,
.item {
  font-weight: 600;
  font-size: 16px;
  color: use(--template-modal-title-color);
  margin: 0;
}
.title {
}
.item {
  opacity: 0.6;
  margin: 0 5px;
  text-transform: capitalize;

  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
}
