@import "sass/index";

.root {
  font-family: "Gilroy";
  width: 100%;
}

.wrapper {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  width: 100%;
  background: transparent;

  & > span {
    width: 50px;
  }
}

.indicator {
  height: 2px;
  background: white;
  border-radius: 2px;
  bottom: 0;
  opacity: 0.3;
}

.tab {
  position: relative;

  background: use(--info-dark) !important;
  opacity: 1 !important;
  border-radius: 10px 10px 0 0;

  margin: 0 15px 0 5px;
  padding: 10px;

  width: fit-content;
  min-width: fit-content;
  max-width: fit-content;

  height: fit-content;
  max-height: fit-content;
  min-height: fit-content;

  overflow: visible;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 21%;
    height: 80%;
    display: block;
    background: use(--info-dark);
    width: 10px;
  }
  &::after {
    width: 15px;
    left: 100%;
    clip-path: polygon(0 0, 0 0%, 100% 100%, 0% 100%);
  }
  &::before {
    width: 5px;
    right: 100%;
    clip-path: polygon(0 0, 0 0%, 100% 100%, 0% 100%);
    transform: scaleX(-1);
  }
}

.tabText {
  font-style: italic;
  font-weight: 900;
  font-size: 20px;
  line-height: 1;
  text-transform: capitalize;

  display: flex;
  align-items: center;

  color: use(--template-modal-title-color);

  @media (max-width: 1100px) {
    font-size: 16px;
  }
}
