.root {
  height: 800px;
  width: 100%;
  & > div {
    overflow-x: hidden;
  }
}
.grid {
  width: 100%;
  & > div {
    position: relative;
    margin: 0 auto;
  }
}
.imageContainer {
  padding: 10px;
  border-radius: 10px;
}
.image {
  width: 100%;
  height: 100%;
  border-radius: 10;
}
.loaderWrapper {
  width: 100%;
  height: 150px;
  display: flex;
  justify-content: center;
  @media (max-width: 600px) {
    align-items: center;
  }
}
.loader {
  @media (max-width: 600px) {
    margin: 0;
  }
}
