.root {
  padding: 10px;
}
.container {
  cursor: pointer;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  position: relative;
  box-shadow: 0px 5px 4px rgba(0, 0, 0, 0.24);
  &:hover {
    & > div {
      visibility: visible;
    }
  }
}
.backgroundImg {
  width: 100%;
  border-radius: 10px;
}
.hover {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(24, 0, 43, 0.8);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  visibility: hidden;

  & > span {
    color: #ffffff;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
  }
}

.button {
  cursor: pointer;
  border: none;
  border-radius: 25px;
  width: 80%;
  background: linear-gradient(276.53deg, #2e2eed -93.34%, #4e4ef9 82.6%);
  height: 40px;
}
