.root {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0;
  font-weight: 500;
  margin-top: -24px;

  @media (max-width: 420px) {
    padding: 0;
  }
}

.loaderWrapper {
  width: 100%;
  height: 100vh;
  min-height: 400px;
  display: flex;
  justify-content: center;
  @media (max-width: 600px) {
    height: 100%;
    align-items: center;
  }
}

.loader {
  margin-top: 25vh;

  @media (max-width: 600px) {
    margin: 0;
  }
}
