.root {
  margin-top: 20px;
}
.controlBox {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 10px;
}
.selectWrapper {
  display: flex;
  gap: 20px;

  width: max-content;
  align-items: center;
  justify-content: flex-end;
}
.selectBox {
  display: flex;
  color: #ffffff;
  font-family: "Gilroy";
  font-size: 16px;
  align-items: center;
  & > span {
    margin-right: 5px;
  }
}
.select {
  padding: 0;
  border: none;
  text-transform: uppercase;
  background-color: transparent;
}

.selectItem {
  & > span {
    text-transform: uppercase;
  }
}

.selectLabel {
  text-transform: uppercase;
  background-color: transparent;
  & > span {
    font-family: "Gilroy";
    font-size: 16px;
    font-weight: 500;
    margin-right: 5px;
  }
}

.selectExpandIcon {
  transform: scale(0.8);
}

.inputPaper {
  font-family: "Gilroy";
  text-transform: uppercase;
}
.selectIcon {
  top: 30%;
  & > g > g {
    stroke: #ffffff;
  }
}
.sortBox {
  cursor: pointer;
}
.arrowIcon {
  & > g > g {
    stroke: #a4a09a;
  }
}
.iconRevert {
  transform: rotate(180deg);
}

.whiteTheme {
  & .selectBox,
  & .select {
    color: #a4a09a;
  }

  & .selectIcon,
  & .arrowIcon {
    & > g > g {
      stroke: #a4a09a;
    }
  }
}

.mobileBox {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;

  & > div > span {
    font-style: italic;
    font-weight: 900;
    font-size: 16px;
    text-transform: capitalize;
    color: Hsla(var(--template-modal-title-color));
    opacity: 1;
  }
}
