.root {
  display: flex;
  margin: -10px -5px 0;

  & > * {
    margin: 10px 5px 0;
  }

  @media (max-width: 900px) {
    flex-wrap: wrap;

    & > * {
      width: calc(50% - 10px);

      &:first-child {
        width: 100%;
      }
    }
  }

  @media (max-width: 600px) {
    margin-top: 20px;
  }

  @media (max-width: 420px) {
    & > * {
      width: 100%;
    }
  }
}

