@import "sass/index";

.header {
  width: 100%;
}

.title {
  font-size: 1.9375rem;
  letter-spacing: -1px;
  font-weight: bold;
  font-family: "Gilroy";
  color: use(--template-modal-title-color);
  margin: 0;
}

.inputWrapper {
  margin: 0;
  width: 100%;
  position: relative;
  display: flex;

  & > div {
    margin: 0;
  }
}

.inputRoot {
  margin: 0;
  border-radius: 8px;
  border: none;
  height: 40px;
  background: use(--info-dark) !important;
}

.input {
  padding-left: 10px;
  color: use(--template-modal-title-color);

  &::placeholder {
    color: use(--template-modal-title-color, 0.7);
  }
}

.inputIconWrapper {
}

.inputIcon {
  margin-left: 10px;
  color: use(--template-modal-title-color);

  cursor: default;
}

.buttonSearch {
  background: use(--info-dark) !important;
  cursor: pointer;
  border: none;
  border-radius: 7px;
  padding: 0 10px;
  height: 40px;
  font-family: "Gilroy";
  font-weight: bold;
  color: use(--template-modal-title-color);
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
  letter-spacing: 1px;

  margin-left: 10px;
}
.searchIcon {
  & path {
    fill: use(--template-modal-title-color);
    fill-opacity: 1;
  }
}
